import { getActClass } from './utils';
import { getHideBaseStyleMixin } from '../mixins/act/hide-base-style';


export default {
  name: 'PressActPromptDialog',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  mixins: [getHideBaseStyleMixin()],
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false,
    },
    showPic: {
      type: Boolean,
      default: false,
      required: false,
    },
    propsData: {
      type: Object,
      default: () => ({}),
    },
    showCloseBtn: {
      type: Boolean,
      default: true,
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    closeDialog() {
      this.$emit('update:show', false);
      this.$emit('onClickCloseButton');
    },
    onClickCancelButton() {
      this.$emit('update:show', false);
      this.$emit('onClickCancelButton');
    },
    onClickButton() {
      this.$emit('clickButton');
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
